<template>
  <div id="checkout">
    <div class="checkout">
      <div class="checkout__main">
        <SfSteps :active="currentStep" @change="updateStep($event)">
          <SfStep name="">
            <SfPersonalDetails
              :value="personalDetails"
              @input="personalDetails = $event"
              :characteristics="characteristics"
              headingTitle=" "
              :inputsLabels="['', ' ', '']"
              :additionalDetails="''"
            >
              <template #log-in>{{ "" }}</template>
              <template #create-account>{{ "" }}</template>
            </SfPersonalDetails>
          </SfStep>
          <SfStep name="  ">
            <SfShipping
              :value="shipping"
              :shipping-methods="shippingMethods"
              :countries="countries"
              @input="shipping = $event"
              headingTitle="  "
              methodsHeadingTitle="   "
              :inputsLabels="inputsLabels"
              selectLabel=""
            />
          </SfStep>
          <SfStep name="">
            <SfPayment
              :paymentMethods="paymentMethods"
              :shipping="shipping"
              :countries="countries"
              :months="months"
              :years="years"
              @input="payment = $event"
              billingHeading="  "
              :billingInputsLabels="paymentInputsLabels"
              sameAsShippingCheckboxLabel="     "
              paymentHeading=" "
              billingSelectLabel=""
            />
          </SfStep>
          <SfStep name=" ">
            <SfConfirmOrder
              :order="getOrder"
              order-title="Order details"
              orderTitle=" "
              :properties-names="[
                ' ',
                '  ',
                ' ',
              ]"
              :table-headers="[
                'Size',
                'Description',
                'Quantity',
                'Colour',
                'Amount',
              ]"
              @click:edit="currentStep = $event"
            >
              <template #table>{{ "" }}</template>
              <template #checkbox>{{ "" }}</template>
            </SfConfirmOrder>
          </SfStep>
        </SfSteps>
      </div>
      <div class="checkout__aside">
        <transition name="sf-fade">
          <SfOrderSummary
            v-if="currentStep <= 2"
            key="order-summary"
            class="checkout__aside-order"
            :order="getOrder"
            order-title=""
            :order-title-level="3"
            :properties-names="[
              '',
              ' ',
              '   ',
              ' ',
            ]"
            :characteristics="characteristics"
          >
            <template #promo>{{ "" }}</template>
          </SfOrderSummary>
          <CustomSfOrderReview
            v-else
            key="order-review"
            class="checkout__aside-order"
            :order="getOrder"
            review-title=""
            :review-title-level="3"
            button-text=""
            :characteristics="characteristics"
            @click:edit="currentStep = $event"
          >
          </CustomSfOrderReview>
        </transition>
      </div>
    </div>
    <div class="actions">
      <SfButton
        class="sf-button--full-width actions__button"
        data-testid="next-button"
        @click="nextStep"
        >{{ steps[currentStep] }}</SfButton
      >
      <SfButton
        class="
          sf-button--full-width sf-button--underlined
          actions__button
          smartphone-only
        "
        @click="checkoutBack"
        ></SfButton
      >
    </div>
  </div>
</template>
<script>
import {
  SfSteps,
  SfButton,
  SfPersonalDetails,
  SfShipping,
  SfPayment,
  SfConfirmOrder,
  SfOrderSummary,
  SfOrderReview,
  SfShippingDetails,
} from "@storefront-ui/vue";
import CustomSfOrderReview from "@/components/CustomSfOrderReview.vue";
export default {
  name: "CheckoutView",
  components: {
    SfSteps,
    SfPersonalDetails,
    SfShipping,
    SfPayment,
    SfConfirmOrder,
    SfOrderSummary,
    SfOrderReview,
    SfButton,
    SfShippingDetails,
    CustomSfOrderReview,
  },
  data() {
    return {
      countries: [""],
      months: [],
      years: [],
      currentStep: 0,
      steps: [
        "  ",
        "  ",
        " ",
        "  ",
      ],
      personalDetails: {
        firstName: "",
        lastName: "",
        email: "test@gmail.com",
      },
      shipping: {
        firstName: "",
        lastName: "",
        streetName: " ",
        apartment: " ",
        city: "",
        state: " ",
        zipCode: "12334",
        country: "",
        phoneNumber: "+989381234567",
        shippingMethod: {
          price: "$0.00",
        },
      },
      payment: {
        sameAsShipping: true,
        firstName: "",
        lastName: "",
        streetName: "",
        apartment: "",
        city: "",
        state: "",
        zipCode: "12234",
        country: "",
        phoneNumber: "+989381234567",
        paymentMethod: "",
        invoice: false,
        cardNumber: "",
        cardHolder: "",
        cardMonth: "",
        cardYear: "",
        cardCVC: "",
        cardKeep: false,
      },
      order: {
        password: "",
        createAccount: false,
        firstName: "",
        lastName: "",
        email: "john.dog@gmail.com",
        orderItems: this.$store.state.cartItems,
      },
      paymentMethods: [
        // {
        //   label: "Visa Debit",
        //   value: "debit",
        // },
        {
          label: " ",
          value: "online",
        },
        // {
        //   label: "Visa Electron",
        //   value: "electron",
        // },
        {
          label: " ",
          value: "cash",
        },
        // {
        //   label: "Check",
        //   value: "check",
        // },
      ],

      inputsLabels: [
        "",
        " ",
        "",
        "",
        "",
        " ",
        " ",
      ],
      paymentInputsLabels: [
        "",
        " ",
        "",
        "",
        "",
        "",
        " ",
        " ",
      ],
      shippingMethods: [
        {
          isOpen: false,
          price: "",
          delivery: "    ",
          label: " ",
          value: "store",
          description:
            "Novelty! From now on you have the option of picking up an order in the selected InPack parceled. Just remember that in the case of orders paid on delivery, only the card payment will be accepted.",
        },
        {
          isOpen: false,
          price: "$9.90",
          delivery: "    ",
          label: " ",
          value: "home",
          description:
            "Novelty! From now on you have the option of picking up an order in the selected InPack parceled. Just remember that in the case of orders paid on delivery, only the card payment will be accepted.",
        },
      ],
      buttonNames: [
        { name: "Go to shipping" },
        { name: "Go to payment" },
        { name: "Review Order" },
        { name: "Place my order" },
      ],
      characteristics: [],
    };
  },
  computed: {
    getOrder() {
      return {
        ...this.order,
        ...this.personalDetails,
        shipping: { ...this.shipping },
        payment: { ...this.payment },
      };
    },
  },
  watch: {
    shipping(newVal, oldVal) {
      if (newVal.shippingMethod != oldVal.shippingMethod) {
        const method = this.shippingMethods.find(
          (method) => method.value === newVal.shippingMethod
        );
        return method
          ? (newVal.shippingMethod = method)
          : (newVal.shippingMethod = { price: "$0.00" });
      }
    },
  },
  methods: {
      checkoutBack(){
             if(this.currentStep==0){
                this.$router.push('/cart')
             }
             else{
              this.currentStep--;

             }


      },
    nextStep() {
      if (this.currentStep < this.steps.length - 1) {
        this.currentStep++;
      } else {
        this.$router.push("/");
      }
    },
    updateStep(next) {
      // prevent to move next by SfStep header

      if (next < this.currentStep) {
        this.currentStep = next;
      }
    },
  },
};
</script>
